/*
* carousels are categorized by the parent class
* To play any slider on a different page, just add
* the new slider class next to any slider below and
* use the same children. Overides calls must come after
* default css
*/
.slider-arrows {
  background: url("../img/sprites/slider_arrows.png");
}

#carousel-our-customers, #carousel-our-customers-two {
  height: 306px;
  width: 100%;
  border: none;
  box-shadow: none;
  display: block;
  position: relative;
  border: 1px solid #010101;
}
#carousel-our-customers *, #carousel-our-customers-two * {
  background: transparent;
}
#carousel-our-customers .carousel-indicators li, #carousel-our-customers-two .carousel-indicators li {
  background: #585d60;
  width: 8px;
  height: 8px;
  border: none;
  border-radius: 1px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  margin: 0 15px 30px 0;
  padding: 0;
}

#carousel-our-customers-two {
  margin: 0 auto;
  height: 425px;
  max-width: 1040px;
}
#carousel-our-customers-two .carousel-indicators {
  top: 105%;
}
#carousel-our-customers-two .carousel-indicators li {
  margin: -50px 15px 30px 0;
}

.sidebar-quotes-slider::before {
  content: "";
  background: transparent;
  background-size: cover;
  opacity: 0.10;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.sidebar-quotes-slider > .quote-slide {
  position: absolute;
  padding: 0 20px;
  font-size: 19pt;
  text-align: center;
  font-family: "Gotham Light";
  display: none;
}

/* Not Used In production */
.sidebar-quotes-slider > .counter {
  display: block;
  top: 18px;
  left: 18px;
  z-index: 999;
}

.sidebar-quotes-slider > .progress {
  width: 0px;
  height: 5px;
  background-color: #f4f4f5;
  display: block;
  bottom: 0;
  left: 0;
  z-index: 999;
}

.sidebar-quotes-slider > .progress::before {
  content: '';
  min-width: 289px;
  width: 289px;
  height: 5px;
  display: block;
  bottom: 0;
  left: 0;
  z-index: 998;
  background-color: #f4f4f5;
  opacity: .25;
}

